import Vue from 'vue'
import Router from 'vue-router'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/photos',
      name: 'photos',
      component: (resolve) => require(['@/pages/Photos'], resolve)
    },
    {
      path: '/photoDetail',
      name: 'photoDetail',
      component: (resolve) => require(['@/pages/PhotoDetail'], resolve)
    },
    {
      path: '/',
      name: 'cover',
      component: (resolve) => require(['@/pages/Cover'], resolve)
    }
  ],
  scrollBehavior (to, from, savedPosition) {
    if (savedPosition) {
      return savedPosition
    } else {
      return { x: 0, y: 0 }
    }
  }
})



